泉禪寺主辦")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("2021/05/19")])]),_c('td',[_c('p',[_vm._v("彌陀禪寺浴佛祈福法會")])]),_c('td',[_c('p',[_vm._v("\n            農曆4月初八為釋迦牟尼佛聖誕，全臺佛寺舉行慶典。嘉義彌陀禪寺為2百多年著名古剎，佛誕當日於早課普佛，後演浴佛儀。全程儀軌完整保存傳統元素，如理如法，於今年各佛誕法會中，更顯殊特難得。其中佛讚〈娑婆界〉為傳統的佛誕節專用讚誦，但現今唱誦者愈來愈少。當日因疫情警戒，浴佛法會沒有對外開放，但祝聖普佛和浴佛儀軌，常住內部照常進行。\n          ")])]),_c('td',[_c('p',[_vm._v("彌陀禪寺主辦")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("2021/09/29")])]),_c('td',[_c('p',[_vm._v("圓照寺110年度傳授在家五戒菩薩戒會")])]),_c('td',[_c('p',[_vm._v("\n            高雄圓照寺舉辦在家五戒菩薩戒會，總共5日。住持敬定法師擔任得戒和尚，金山寺住持修熏法師為羯摩阿闍黎及開堂大師父，淨珠法師為教授阿闍黎。菩薩戒，有受法而無捨法，為三聚淨戒，所謂攝律儀戒、攝善法戒、饒益有情戒，是過去現在未來一切菩薩之學處。敬定法師說明：以此戒會的殊勝功德，祈願息災護國。\n          ")])]),_c('td',[_c('p',[_vm._v("圓照寺主辦")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("2021/11/05-11/07")])]),_c('td',[_c('p',[_vm._v("果碩大慈普利寺藥師佛千供法會")])]),_c('td',[_c('p',[_vm._v("\n            屏東果碩大慈普利寺屬於藏傳佛教格魯派，是北頂法王，怙主果碩仁波切在臺灣的根本道場。藥師佛千供法會，是其一年之中的最大法會，係依印度色拉寺傳承，修誦第五世觀音尊者所著之《藥師七佛供養儀軌－如意自在王》。今年由桑珠格西主法，賈沓格西為維那，以藏語念誦帶領大眾共修。修法中的「千供」，包含千盞燈、2千杯水、千支香千朵花（合為一杯）、千杯塗香、千食子，共6千杯供品。念誦亦須為千遍。\n          ")])]),_c('td',[_c('p',[_vm._v("果碩大慈普利寺主辦")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("2021/11/27")])]),_c('td',[_c('p',[_vm._v("靈泉禪寺方丈陞座大典")])]),_c('td',[_c('p',[_vm._v("\n            基隆月眉山靈泉禪寺是臺灣佛教重要法脈的本山，第十任方丈和尚上惟下盛法師陞座典禮，諸山長老送座證盟，全臺月眉山的法脈末寺法師和信徒皆來觀禮。儀典具重要的表法與傳承象徵，莊嚴隆重，含交接印信、掛大珠、送位、說法、長老咐囑等，共12節儀程。\n          ")])]),_c('td',[_c('p',[_vm._v("靈泉禪寺主辦")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("2021/11/20-12/04")])]),_c('td',[_c('p',[_vm._v("佛光山萬緣水陸法會（二期）")])]),_c('td',[_c('p',[_vm._v("\n            佛光山的萬緣水陸法會，於儀軌唱念、修法、道場嚴飾等軌則，隆重莊嚴，每年參加的人員、信眾極多，是佛教界的大活動。今年因疫情影響，雖然內、外壇之儀軌依循規制，然而運用的技術、人力、場地規劃，實別於往常，為時勢造就的新水陸佛事。內壇儀軌一樣於本山舉辦，而外壇諸壇則結合各分別院，分南北區域，共成外壇佛事，其中大壇佛事同步線上直播，開放民眾參與共修。內壇佛事，除了部分內壇齋主參與，也提供直播，讓未入內壇壇場的水陸頭可以在佛光山，或各分別院，以線上方式參與法會。\n          ")])]),_c('td',[_c('p',[_vm._v("佛光山主辦")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("2021/11/28-12/04")])]),_c('td',[_c('p',[_vm._v("靈巖山寺雙溪小築啟建第七屆百七護國息災三時繫念法會－第一七")])]),_c('td',[_c('p',[_vm._v("\n            新北市雙溪小築百七護國息災三時繫念法會，在佛教界、佛教歷史、甚至在各宗教的活動中，可謂創舉。所謂「百七」，即一百個七期。七日為一期，百七為700日，以《三時繫念佛事》為功課，每天不間斷的修法。而「護國息災」，顧名思義，為求消弭減輕臺灣及全球的重大災難。今年11月雙溪小築完成第六屆百七《三時繫念佛事》，於28日正式啟建第七屆的百七，將滿12年。\n          ")])]),_c('td',[_c('p',[_vm._v("靈巖山寺雙溪小築主辦")])])])])])])}]

