('p',[_vm._v("\n            農曆5月20日是為邵族一年一度的水田整田祭，祭典從08:30開始，於南投縣魚池鄉伊達邵社區公有祭祀場，由陳忠成長老領唱。祭品為盛米籃（qafay）盛裝米飯，但因應疫情，今年建議家族或鄰居家戶委派1位代表拿祭品至祭場。\n          ")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("農曆6月3日")])]),_c('td',[_c('p',[_vm._v("水田祈安祭")])]),_c('td',[_c('p',[_vm._v("\n            每年農曆6月3日，為邵族的水田祈安祭Mulalu Muramaz，而muramaz\n            是穩的意思。透過水田祈安祭祈禱水稻收穫平安。儀式於陳姓祭司家前，在盛米籃（qafay）裡擺放一些白米，每一個籃子代表一戶邵族人家。讓5位先生媽（女祭司）shinshii唸禱，請祖靈前來享用，並祖靈報告以及祈求族人種植的水田農作能夠安穩豐收。\n          ")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("農曆7月1日")])]),_c('td',[_c('p',[_vm._v("狩獵祭")])]),_c('td',[_c('p',[_vm._v("\n            狩獵祭Matansuun為邵族傳統祭儀活動，於每年的農曆7月1日時進行。每戶搬出祖靈籃，籃中供奉祖先的衣物、飾物。祭儀分成兩組進行石氏頭人25家戶以及袁氏頭人24家戶，族人們將自家的祖靈籃分別放置於袁氏及石氏頭人家，石氏頭人家由石至寶祭司、石累祭司以及陳賢美祭司與祖靈對話；袁氏頭人家由石玉英祭司、毛美麗祭司與祖靈對話，一同祈求祖靈庇佑族人上山狩獵及湖邊捕魚都能平安與豐收。祭儀中所唱的歌曲為打獵歌，屬於平日也可以傳唱的古歌。\n          ")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("農曆7月3日")])]),_c('td',[_c('p',[_vm._v("白鰻祭")])]),_c('td',[_c('p',[_vm._v("\n            農曆7月3日，各戶家主會準備白鰻麻糬，每一條白鰻麻糬上都貼有姓名紅紙，並將自家公媽籃拿到頭人家中，請先生媽隆重祭禱，灑酒恭請祖靈前來享用，同時也請祖靈保佑子孫平安、豐收。\n          ")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("農曆8月1日")])]),_c('td',[_c('p',[_vm._v("祖靈祭")])]),_c('td',[_c('p',[_vm._v("\n            農曆7月的最後一天，邵族人開始奏杵音，早期舂石音只能由部落的婦女敲奏，現今已無此限制。在過年的第一天，由先生媽主持祭祀祖靈的儀式，部落內所有的男性族人都要參加擦手臂除檅儀式。在月中之後，年輕族人於白天工作，夜間才繼續歌舞為樂。整個祭儀期間，分別有為8歲至12歲孩童舉行的鑿齒儀式與蓋祖靈屋，是先生媽的專屬工作，祭儀進行一半，須舉行甜酒祭祖靈的儀式，並將代表邵族最高祖靈的日月盾牌，供奉在祖靈屋內。\n          ")])])])])]),_c('br'),_c('h3',[_vm._v("十一、噶瑪蘭族")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","border":"1","cellpadding":"5"}},[_c('thead',[_c('tr',[_c('th',{staticStyle:{"width":"20%"}},[_vm._v("時間")]),_c('th',{staticStyle:{"width":"20%"}},[_vm._v("活動名稱")]),_c('th',{staticStyle:{"width":"25%"}},[_vm._v("活動內容")]),_c('th',{staticStyle:{"width":"25%"}},[_vm._v("備註")])])]),_c('tbody',[_c('tr',[_c('td',[_c('p',[_vm._v("4月、7月、8月")])]),_c('td',[_c('p',[_vm._v("海祭（拜海）")])]),_c('td',[_c('p',[_vm._v("\n            噶瑪蘭族選擇一到數天的時間，在靠近部落的海灘上舉行，祭拜祖靈及掌管海洋的神靈，祈求航海順利、漁獲豐收。\n          ")])]),_c('t