"十四、賽德克族")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","border":"1","cellpadding":"5"}},[_c('thead',[_c('tr',[_c('th',{staticStyle:{"width":"20%"}},[_vm._v("時間")]),_c('th',{staticStyle:{"width":"20%"}},[_vm._v("活動主題")]),_c('th',[_vm._v("活動內容")])])]),_c('tbody',[_c('tr',[_c('td',[_c('p',[_vm._v("2月至3月")])]),_c('td',[_c('p',[_vm._v("播種祭")])]),_c('td',[_c('p',[_vm._v("\n            小米播種祭約於每年的2-3月間舉行，小米播種祭約進行三天兩夜，在族人遵循族規（Gaya）的情境下莊嚴肅穆地進行。第一天以家庭為單位，要召回所有的家庭成員返回部落參加祭祀活動，當天傍晚要儲備食物，獵狗要栓在屋內，就寢之前大家要保持安靜，全區域進入宵禁狀態；第二天全體族人只能待在自家屋裡，不得喧嘩、不得生火或煮食、不可縫衣織布、不可使用刀具利器等；第三天各部落族人於拂曉之前即起，燃舉火把、扶老攜幼前往指定的祭祀祭壇，在副祭司的帶領下依序進行「Cmucuc」的祈求豐收儀式。\n          ")])])]),_c('tr',[_c('td',[_c('p',[_vm._v("12月31日")])]),_c('td',[_c('p',[_vm._v("收穫祭（祖靈祭）")])]),_c('td',[_c('p',[_vm._v("\n            收穫祭約於每年的12月31日舉行，其活動規模與播種祭相同，要遵守的族規也與播種祭雷同，即由部落領導人協同長老數人同赴主祭司的家中商討關收穫事宜，決定祭祀活動的時日；舉行收穫祭當天，收穫祭祀團於天亮之前要完成採集小米穗約3把，但每一把小米穗中的小米穗串是由不同的小米田所摘取的，並攜至收穫祭壇進行祭祀儀式。收穫祭祀團於天色初明之際返回部落主祭司的家中，此時各部落的領導人已在那兒恭候收穫祭祀團的到來。各部落的領導人會攜帶各式各樣的饋贈品送給收穫祭祀團，短暫歡聚之後，各部落領導人即返回各自的部落，向部落族人宣佈可以開始採收小米。\n          ")])])])])]),_c('br'),_c('h3',[_vm._v("十五、拉阿魯哇族")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","border":"1","cellpadding":"5"}},[_c('thead',[_c('tr',[_c('th',{staticStyle:{"width":"20%"}},[_vm._v("時間")]),_c('th',{staticStyle:{"width":"20%"}},[_vm._v("活動名稱")]),_c('th',[_vm._v("活動內容")])])]),_c('tbody',[_c('tr',[_c('td',[_c('p',[_vm._v("2021/03/20-03/21")])]),_c('td',[_c('p',[_vm._v("聖貝祭")])]),_c('td',[_c('p',[_vm._v("\n            拉阿魯哇族最重要的祭儀，原為美壠社特有的祭儀，現在則有雁爾社、排剪社和美壠社三社同時舉行此大祭。\n          ")])])])])]),_c('br'),_c('h3',[_vm._v("十六、卡那卡那富族")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","border":"1","cellpadding":"5"}},[_c('thead',[_c('tr',[_c('th',{staticStyle:{"width":"20%"}},[_vm._v("時間")]),_c('th',{staticStyle:{"width":"20%"}},[_vm._v("活動名稱")]),_c('th',{staticStyle:{"width":"25%"}},[_vm._v("活動內容")]),_c('th',{staticStyle:{"width":"25%"}},[_vm._v("備註")])])]),_c('tbody',[_c('tr',[_c('td',[_c('p',[_vm._v("2021/10/09")])]),_c('td',[_c('p',[_vm._v("米貢祭")])]),_c('td',[_c('p',[_vm._v("\n            「米貢」在卡那卡那富族語為小米的意思，米貢祭在小米收成時舉行，意指一年的結束，祈求祖靈神與地神庇佑族人。\n          ")])]),_c('td',[_c('p',[_vm._v("臺灣卡那卡那富族發展協會主辦")])])])])]),_c('br'),_c('h3',[_vm._v("十七、平埔族－大武壠族")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","border":"1","cellpadding":"5"}},[_c('thead',[_c('tr',[_c('th',{staticStyle:{"width":"20%"}},[_vm._v("時間")]),_c('th',{staticStyle:{"width":"20%"}},[_vm._v("活動名稱")]),_c('th',{staticStyle:{"width":"25%"}},[_vm._v("活動內容")]),_c('th',{staticStyle:{"width":"25%"}},[_vm._v("備註")])])]),_c('tbody',[_c('tr',[_c('td',[_c('p',[_vm._v("2021/04/24")])]),_c('td',[_c('p',[_vm._v("第七屆大武壠歌舞文化節")])]),_c('td',[_c('p',[_vm._v("\n            大武壠歌舞文化節結合傳統文化概念，近幾年的文化節主題是「乞雨」，族人重新呈現大武壠消失60年的「乞雨儀式」。除了主儀式之外，今年亦有《回家跳舞》新書發表會，樂舞表演包含小林國小陶笛演奏、日光小林大鼓陣及其他友好團體交流演出。\n          ")])]),_c('td',[_c('p',[_vm._v("高雄市杉林區日光小林社區發展協會、大滿舞團主辦")])])])])]),_c('br'),_c('h3',[_vm._v("十八、平埔族－馬卡道族")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","border":"1","cellpadding":"5"}},[_c('thead',[_c('tr',[_c('th',{staticStyle:{"width":"20%"}},[_vm._v("時間")]),_c('th',{staticStyle:{"width":"20%"}},[_vm._v("活動名稱")]),_c('th',[_vm._v("活動內容")])])]),_c('tbody',[_c('tr',[_c('td',[_c('p',[_vm._v("2021/11/19、12/18-12/19、2022/02/12")])]),_c('td',[_c('p',[_vm._v("屏東縣馬卡道族聯合夜祭")])]),_c('td',[_c('p',[_vm._v("\n            屏東縣馬卡道族聯合夜祭共計3個場次，包含農曆10月15日（國曆11月19日）的內埔老埤、農曆11月15日（國曆12月18日、19日）高樹加蚋埔、農曆正月15日（國曆2022年2月12日）的萬巒加匏朗夜祭。\n          ")])])])])]),_c('br'),_c('h3',[_vm._v("十九、平埔族－西拉雅族")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","border":"1","cellpadding":"5"}},[_c('thead',[_c('tr',[_c('th',{staticStyle:{"width":"20%"}},[_vm._v("時間")]),_c('th',{staticStyle:{"width":"20%"}},[_vm._v("活動名稱")]),_c('th',[_vm._v("活動內容")])])]),_c('tbody',[_c('tr',[_c('td',[_c('p',[_vm._v("2021/10/09")])]),_c('td',[_c('p',[_vm._v("Uma Talavan（萬淑娟）獲第三十二屆傳藝金曲獎最佳作詞獎")])]